@import url(https://fonts.googleapis.com/css?family=Merriweather:400,700,900);
@import url(https://fonts.googleapis.com/css?family=Oxygen:400,300,700);
.main-container {
  background-color: #fff;
}

.body-section {
  padding-bottom: 20px;
}

a {
  color: rgb(0, 136, 204);
}

/*
    ====================
     HEADER
    ====================
*/
.header-img {
  background: #3e668b !important;
}

