article header {
  padding-top: 0;
}

header {
  padding: 10px 0 0 0;
}
header img {
  margin: 0 0 -2px -4px;
}
@media only screen and (max-width: 420px) {
  header {
    padding: 6px 0 0 0;
  }
  header img {
    width: 319px;
    margin: 0 0 -1px -4px;
  }
}
@media only screen and (min-width: 421px) and (max-width: 740px) {
  header {
    padding: 6px 0 0 0;
  }
  header img {
    width: 95%;
    margin: 0 0 -1px -4px;
  }
}
@media only screen and (min-width: 741px) {
  header {
    padding: 10px 0 0 0;
  }
}
@media only screen and (min-width: 1141px) {
  header {
    padding: 20px 0 0 0;
  }
}

.social_links {
  float: right;
  width: 98px;
  height: 24px;
  margin: 16px 0 0 10px;
  padding: 0;
  text-align: left;
}
.social_links img {
  width: 24px;
  height: 24px;
  margin: 1px;
}
.social_links .email {
  position: absolute;
  color: #fff;
  font-family: "Merriweather", serif;
  font-size: 1.4rem;
  font-weight: 900;
  text-decoration: none;
  margin: 0 0 1px 5px;
}
@media only screen and (max-width: 420px) {
  .social_links {
    margin: 0 0 0 40px;
  }
  .social_links img {
    width: 16px;
    height: 16px;
  }
  .social_links .email {
    font-size: 0.9rem;
    font-weight: 700;
  }
}
@media only screen and (min-width: 421px) and (max-width: 740px) {
  .social_links {
    margin: 4px 0 0 40px;
  }
  .social_links img {
    width: 20px;
    height: 20px;
  }
  .social_links .email {
    font-size: 1.1rem;
    font-weight: 700;
  }
}
/*
    ====================
     MAIN NAV
    ====================
*/
.main-nav .nav-tabs ul {
  margin: 16px 0 0 0;
  padding: 0 10px 0 10px;
  border-bottom: solid 1px #ddd;
}
.main-nav .nav-tabs ul li {
  margin: 0 0 -1px 0;
  padding: 3px 12px;
  display: inline-block;
  border-bottom: solid 1px #ddd;
}
.main-nav .nav-tabs ul li a {
  font-family: Helvetica, Arial, sans-serif;
  font-size: 0.9rem;
  margin: 0;
  padding: 0;
  text-decoration: none;
}
.main-nav .nav-tabs ul li.active {
  -moz-border-radius-topleft: 4px;
  border-top-left-radius: 4px;
  -moz-border-radius-topright: 4px;
  border-top-right-radius: 4px;
  border-left: solid 1px #ddd;
  border-top: solid 1px #ddd;
  border-right: solid 1px #ddd;
  border-bottom: solid 1px #fff;
}
.main-nav .nav-tabs ul li.active a {
  color: #555;
}
.main-nav .current-page {
  padding-left: 6px;
  padding-top: 6px;
}
.main-nav .current-page ul {
  margin: 0;
  padding: 0 0 12px 0;
}
.main-nav .current-page ul li {
  margin: 0;
  padding: 0 2px 0 10px;
  display: inline-block;
}
.main-nav .current-page ul li span {
  background: url("../img/menu-arrow.png");
  width: 6px;
  height: 9px;
  top: 19px;
  position: relative;
  display: block;
  margin: 0 0 2px -10px;
}
.main-nav .current-page ul li a {
  font-family: "Merriweather", serif;
  font-weight: normal;
  color: #555;
  text-decoration: none;
}
.main-nav .current-page ul li a:hover {
  color: #000;
  text-decoration: none;
}
.main-nav .current-page ul li.active a {
  color: #222;
  font-weight: 400;
}

/*
    ====================
     FOOTER
    ====================
*/
.footer-container {
  font-size: 1rem;
  margin: 0;
  padding: 20px 0 16px 0;
}
.footer-container footer {
  color: #555;
}
.footer-container footer span {
  display: inline;
}
@media only screen and (max-width: 420px) {
  .footer-container {
    padding: 6px 0 4px 0;
  }
  .footer-container footer span {
    display: none;
  }
}
@media only screen and (min-width: 421px) and (max-width: 740px) {
  .footer-container {
    padding: 16px 0 10px 0;
  }
}
@media only screen and (min-width: 741px) {
  .footer-container {
    padding: 20px 0 16px 0;
  }
}
@media only screen and (min-width: 1141px) {
  .footer-container {
    padding: 26px 0 20px 0;
  }
}

/*
    ====================
     TYPOGRAPHY
    ====================
*/
h1, h2, h3, h4 {
  text-transform: lowercase;
}

h1, h2, h3, h4, h5, h6,
h1 a, h2 a, h3 a, h4 a, h5 a, h6 a {
  clear: both;
  font-family: "Merriweather", Cambria, serif;
  font-weight: bold;
  text-decoration: none;
}

h1, h1 a {
  font-size: 2rem;
  line-height: 2.5rem;
}

p, div, span, a, td {
  font-family: "Oxygen", Helvetica, "Helvetica Neue", Arial, sans-serif;
  font-size: 1rem;
  font-weight: normal;
}

/*
    ====================
     CONTENT LAYOUT
    ====================
*/
.main aside {
  background-color: #fff;
  padding: 14px 8px 14px 14px;
  margin-left: 14px;
  /*border-left: dashed 1px #3e668b;*/
  border-left: dashed 1px #ddd;
}
.main aside ul {
  padding: 0 0 0 20px;
}
.main aside ul li span {
  color: #888;
}
@media only screen and (max-width: 740px) {
  .main aside {
    margin: 0;
    padding: 2px;
    border-left: solid 0;
    border-bottom: dashed 1px #3e668b;
  }
}
@media only screen and (min-width: 741px) {
  .main article {
    float: left;
    width: 65%;
  }
  .main aside {
    float: right;
    width: 28%;
  }
}

@media only screen and (min-width: 741px) {
  .Blog article {
    width: 80%;
  }
  .Blog article header div {
    margin: -20px 10px 20px 0;
  }
  .Blog article header div .posted {
    margin-right: 20px;
  }
  .Blog article header div .labels {
    color: #888;
  }
}

/*
    ====================
     LAYOUT HELPERS
    ====================
*/
/* MOBILE */
.block {
  display: block;
}

/* DESKTOP */
@media only screen and (min-width: 1141px) {
  .right {
    float: right;
  }
}
article section {
  /* MOBILE */
  /* DESKTOP */
}
article section .half {
  width: 98%;
}
article section .right {
  float: none;
  margin-top: 0;
}
@media only screen and (min-width: 1141px) {
  article section .right {
    float: right;
  }
  article section .half {
    width: 48%;
  }
}